import { Grid, Paper, Table, TableCell, TableContainer, TableHead, TableRow, Typography } from '@mui/material'
import React from 'react'
import { NormalButton } from '../buttons/NormalButton'
import { NumberText } from '../inputs/NumberText'

export const ItemPago = ({ pago, deletePago }) => {
    return (
        <TableRow
            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
        >
            <TableCell component="th" scope="row">
                <NumberText value={pago.valor_pago} />
            </TableCell>
            <TableCell >{pago.fecha_pago.split('T')[0]}</TableCell>
            <TableCell >
                <NumberText value={pago.valor_capital} />
            </TableCell>
            <TableCell >
                <NumberText value={pago.valor_interes} />
            </TableCell>
            <TableCell >
                <NormalButton fullWidth={false} text="Eliminar" sx={{background:'red'}} onClick={()=>deletePago(pago._id)} />
            </TableCell>
        </TableRow>

    )
}
