import { Container, Divider, Grid, IconButton, TextField, styled, Typography, Checkbox } from '@mui/material';
import Modal from '@mui/material/Modal';
import { Box } from '@mui/system'
import moment from 'moment';
import React from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { theme } from '../../configtheme'
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen'
import { GeneratePago, GetPrestamosByCliente, GetProximosCobrar, ReCalculateInteres } from '../../redux/actions/prestamo.actions';
import { uiCloseModal, uiErrorMessage } from '../../redux/actions/ui.actions'
import { NormalButton } from '../buttons/NormalButton';
import { InputNormalNumber } from '../inputs/InputNormalNumber';
import { QsTitle } from '../QuickSearch/QsTitle'



export const ModalPagar = ({ title, fecha_ini, fecha_fin }) => {
    const isMobile = useCheckMobileScreen();
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        // height: '70vh',
        transform: 'translate(-50%, -50%)',
        width: `${isMobile ? '80%' : '60%'}`,
        bgcolor: 'background.paper !important',
        border: `1px solid ${theme.palette.valen.tertiary}`,
        borderRadius: 8,
        boxShadow: 24,
        p: 4,
        background: 'white'
    };

    const [valor_pagar, setValor_pagar] = useState('');
    const [valor_interes, setValor_interes] = useState('');
    const [fecha_pago, setFecha_pago] = useState('');
    const [fecha_ultimo_pago, setFecha_ultimo_pago] = useState('');
    const [check, setCheck] = useState(false);
    const [checkFin, setCheckFin] = useState(false);
    const dispatch = useDispatch();
    const { modalOpen, optionSelected } = useSelector(state => state.ui);
    const { prestamo } = useSelector(state => state.prestamo);

    useEffect(() => {
        getFechaUltimoPago();
    }, [prestamo])

    useEffect(() => {
        if (!modalOpen) {
            setFecha_pago(moment().format('YYYY-MM-DD'));
            setValor_pagar('');
            setValor_interes('');
            setFecha_ultimo_pago('');
            setCheck(false);
            setCheckFin(false);
        }
    }, [modalOpen])


    const getFechaUltimoPago = () => {
        if (prestamo?.pagos?.length > 0) {
            let maxDateObject = prestamo.pagos.reduce((prev, curr) => {
                let prevDate = new Date(prev.date);
                let currDate = new Date(curr.date);
                return (prevDate > currDate) ? prev : curr;
            });
            setFecha_ultimo_pago(maxDateObject?.fecha_pago);
        }
    }

    const handleChange = (e) => {
        setValor_pagar(e.target.value);
    }

    const handleChangeInteres = e => setValor_interes(e.target.value);

    const handleChangeFechaPago = (e) => {
        setFecha_pago(e.target.value);
    }

    const selectInteres = () => {
        console.log(prestamo);
        setCheck(prev => !prev);
        const valor_pagar = prestamo.capital_actual * (prestamo.tasa_interes / 100)
        if (!check) {
            setValor_pagar(valor_pagar);
            setValor_interes(valor_pagar);
        } else {
            setValor_pagar('');
            setValor_interes('');
        }
    }

    const selectFinPrestamo = () => {
        setCheckFin(prev => !prev);
    }

    const RecalcularInteres = (e) => {
        if (!fecha_pago) return dispatch(uiErrorMessage('Digite la fecha de pago correctamente'))
        dispatch(ReCalculateInteres(prestamo._id, moment(fecha_pago)));
    }

    const handleClose = () => {
        dispatch(uiCloseModal());
    }

    const handlePagar = async (e) => {
        // e.preventDefault();
        if (!valor_pagar) return dispatch(uiErrorMessage("Digite correctamente el valor a pagar"));
        if (!fecha_pago) return dispatch(uiErrorMessage("Digite correctamente la fecha de pago"));
        if (!valor_interes) return dispatch(uiErrorMessage("Digite correctamente el valor del interes"));
        console.log(valor_pagar);
        console.log(fecha_pago);
        console.log(valor_interes);

        await dispatch(GeneratePago(prestamo._id, valor_pagar, fecha_pago, valor_interes, checkFin));
        if (optionSelected === 'Pagos') await dispatch(GetPrestamosByCliente(prestamo.id_cliente));
        if (optionSelected === 'Proximos a cobrar') await dispatch(GetProximosCobrar(fecha_ini, fecha_fin))
        handleClose();
    }
    function isEmpty(obj) {
        return Object.keys(obj).length === 0;
    }
    return (
        <div>
            {
                !isEmpty(prestamo) && (
                    <Modal
                        open={modalOpen}
                        onClose={handleClose}
                        aria-labelledby="modal-modal-title"
                        aria-describedby="modal-modal-description"
                    >
                        <Box sx={style}>
                            <QsTitle title={title} handleClose={handleClose} />
                            <Divider sx={{ mt: 2, mb: 2 }} />
                            <Container component="main" maxWidth="lg" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }} >
                                <Grid container spacing={2} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', alignContent: 'center' }}>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Cliente: {check} {prestamo.cliente}</Typography>
                                    </Grid>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Fecha prestamo: {prestamo.fecha_prestamo.split('T')[0]}</Typography>
                                    </Grid>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Valor Prestamo: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.valor_prestamo)}</Typography>
                                    </Grid>
                                    <Grid item xs={12} md={6}>
                                        <Typography>Capital Actual: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.capital_actual)}</Typography>
                                    </Grid>

                                    <Grid item xs={12} md={6}>
                                        <Typography> Interes Real : {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.valor_interes)}</Typography>
                                    </Grid>

                                    <Grid item xs={12} md={6}>
                                        <Typography > <b>Fecha Corte</b>: {fecha_ultimo_pago.split('T')[0]}</Typography>
                                    </Grid>

                                    <Grid item xs={12} md={3}>
                                        <TextField
                                            type="date"
                                            margin="dense"
                                            id="fecha_pago"
                                            label="Fecha pago"
                                            name="fecha_pago"
                                            InputLabelProps={{ shrink: true }}
                                            onChange={handleChangeFechaPago}
                                            onBlur={RecalcularInteres}
                                            value={fecha_pago}
                                            prefix="$"
                                        />
                                    </Grid>

                                    <Grid item xs={12} md={4}>
                                        <InputNormalNumber
                                            margin="dense"
                                            id="valor_pagar"
                                            label="Valor a Pagar"
                                            name="valor_pagar"
                                            onChange={handleChange}
                                            value={valor_pagar}
                                            prefix="$"
                                        />
                                    </Grid>

                                    <Grid item xs={12} md={4} style={{ display: 'flex', justifyContent: 'center' }}>
                                        <InputNormalNumber
                                            margin="dense"
                                            id="valor_interes"
                                            label="Valor Interes"
                                            name="valor_interes"
                                            onChange={handleChangeInteres}
                                            value={valor_interes}
                                            prefix="$"
                                        />
                                        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignContent: 'center', alignItems: 'center' }}>
                                            <span>{prestamo.tasa_interes}%</span>
                                            <Checkbox onChange={selectInteres} value={check} />
                                        </div>
                                    </Grid>

                                    <NormalButton text="Pagar" onClick={handlePagar} />
                                    <Checkbox title='Al seleccionar aqui se da el credito por pagado' onChange={selectFinPrestamo} value={checkFin} />
                                    Finiquitar Prestamo
                                </Grid>
                            </Container>


                        </Box>
                    </Modal>
                )
            }

        </div>
    );
}
