import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { ItemPago } from './ItemPago';
import { Grid, Paper, Table, TableCell, TableContainer, TableHead, TableRow, Card, CardContent, Typography, TableBody, CardHeader } from '@mui/material'
import { NumberText } from '../inputs/NumberText';
import { DeletePago, GetPrestamosByCliente, GetProximosCobrar } from '../../redux/actions/prestamo.actions';

export const CardPagos = ({fecha_ini,fecha_fin}) => {
    const dispatch = useDispatch();
    const { prestamo } = useSelector(state => state.prestamo);
    const { modalOpen,optionSelected } = useSelector(state => state.ui);

    useEffect(() => {
      console.log(prestamo);
    }, [prestamo])
    

    const deletePago = async(pago_id) => {
        await dispatch(DeletePago(pago_id,prestamo._id));
        if(optionSelected==='Pagos') await dispatch(GetPrestamosByCliente(prestamo.id_cliente));
        if(optionSelected==='Proximos a cobrar')await dispatch(GetProximosCobrar(fecha_ini,fecha_fin))

    }

    return (
        <>
            <Card sx={{ mt: 2 }} elevation={3}>
                <CardHeader> Info del prestamo</CardHeader>
                <CardContent>
                    <Typography>Cliente: {prestamo.cliente}</Typography>
                    <Typography>Fecha: {prestamo.fecha_prestamo.split('T')[0]}</Typography>
                    <Typography>Valor: {new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(prestamo.valor_prestamo)}</Typography>
                    <Typography>Saldo: <NumberText value={prestamo.capital_actual} /> </Typography>
                    <>
                        <TableContainer component={Paper}>
                            <Table aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>Pago</TableCell>
                                        <TableCell >Fecha</TableCell>
                                        <TableCell >Capital</TableCell>
                                        <TableCell >Interes</TableCell>
                                        <TableCell ></TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {prestamo?.pagos && prestamo?.pagos.map((pago, index) => (
                                        <ItemPago key={index} pago={pago} deletePago={deletePago} />
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </>
                </CardContent>
            </Card>
        </>
    )
}
